/* App.css - Cutesy & Happy Birthday Edition! */

/* General Styles */
body {
  margin: 0;
  /* Soft pastel background, like a gently swirled frosting */
  background-color: #ffe8f4; /* Light pink */
  font-family: 'Comic Sans MS', cursive, sans-serif; /* Playful font */
  color: #5d4037; /* Warm brown for text */
  line-height: 1.6;
  overflow-x: hidden; /* Prevent horizontal scroll from animations */
}

/* Add some confetti! */
body::before, body::after {
  content: '';
  position: fixed;
  top: -100px;
  width: 10px;
  height: 10px;
  background-color: #ff99cc; /* Pink */
  border-radius: 50%;
  animation: confettiFall 10s infinite ease-in-out;
  z-index: -1;
}

body::before {
  left: 10%;
  animation-delay: 0s;
  background-color: #a7e9af; /* Green */
}

body::after {
  left: 70%;
  animation-delay: 3s;
  background-color: #99ccff; /* Blue */
  width: 15px;
  height: 15px;
}

@keyframes confettiFall {
  0% { transform: translateY(-100px) rotate(0deg); opacity: 1; }
  100% { transform: translateY(calc(100vh + 100px)) rotate(720deg); opacity: 0; }
}


h1, h2, h3 {
  font-family: 'Bubblegum Sans', cursive, sans-serif; /* Even more playful font */
  color: #ff69b4; /* Hot pink! */
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1); /* Soft shadow */
}

a {
  text-decoration: none;
  color: #ff99cc; /* Pink link color */
  transition: color 0.3s ease, transform 0.2s ease;
}

a:hover {
  color: #ff69b4; /* Darker pink on hover */
  transform: translateY(-2px); /* Slight lift */
}

/* App Container */
.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  position: relative; /* For any absolute positioning inside */
  z-index: 1; /* Keep content above confetti */
}

/* --- NEW CSS FOR HIDING/SHOWING CONTENT --- */
.content-to-toggle.hidden-content {
  opacity: 0;
  visibility: hidden;
  max-height: 0;
  overflow: hidden;
  transition: opacity 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94), visibility 0.7s, max-height 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

.content-to-toggle.show-content {
  opacity: 1;
  visibility: visible;
  max-height: 5000px;
  animation: fadeInPop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards; /* Pop effect */
  transition: opacity 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94), visibility 0.7s, max-height 0.7s cubic-bezier(0.25, 0.46, 0.45, 0.94);
}

/* Fade-in Pop Animation */
@keyframes fadeInPop {
  from {
      opacity: 0;
      transform: scale(0.9) translateY(20px);
  }
  to {
      opacity: 1;
      transform: scale(1) translateY(0);
  }
}

/* Button Styles */
.toggle-button {
  background-color: #ff69b4; /* Hot pink button */
  color: white;
  border: none;
  padding: 15px 25px;
  border-radius: 30px; /* Pill shape */
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  letter-spacing: 1px;
  transition: background-color 0.3s ease, transform 0.2s ease, box-shadow 0.3s ease;
  margin-bottom: 25px;
  box-shadow: 0 5px 15px rgba(255, 105, 180, 0.4); /* Pink shadow */
  text-transform: uppercase; /* Make it pop! */
}

.toggle-button:hover {
  background-color: #e05e9e; /* Slightly darker pink on hover */
  transform: scale(1.08) rotate(-2deg); /* Slightly larger and tilted */
  box-shadow: 0 8px 20px rgba(255, 105, 180, 0.6);
}

/* Card Styles */
.card {
  background: linear-gradient(135deg, #ffffff, #fff0f5); /* Soft gradient */
  border-radius: 15px; /* More rounded */
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.15); /* Deeper shadow */
  padding: 30px;
  margin-bottom: 25px;
  text-align: center;
  border: 2px dashed #ffb6c1; /* Cute dashed border */
  position: relative;
  overflow: hidden; /* For cute corner elements */
}

/* Cute corner ribbon */
.card::before {
  content: 'ðŸŽ‰'; /* Party popper emoji */
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 2em;
  transform: rotate(20deg);
}


/* Bio Section */
.bio-section {
  background: linear-gradient(135deg, #fcefee, #fff7fa);
  border-radius: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  padding: 30px;
  margin-bottom: 25px;
  border: 1px solid #ffd1dc; /* Soft pink border */
}

.bio-section h2 {
  font-size: 28px;
  margin-bottom: 20px;
  color: #ff69b4;
  text-align: center;
}

.bio-section p {
  font-size: 17px;
  line-height: 1.8;
  color: #777;
}

/* Pictures Section */
.pictures-section {
  background: linear-gradient(135deg, #fdf9ff, #f3e9f5);
  border-radius: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  padding: 30px;
  margin-bottom: 25px;
  border: 1px solid #d4a7f0; /* Soft purple border */
}

.pictures-section h2 {
  font-size: 26px;
  margin-bottom: 20px;
  color: #8a2be2; /* Blue violet for pictures */
  text-align: center;
}

.pictures-container {
  display: flex;
  gap: 20px; /* More space */
  flex-wrap: wrap;
  justify-content: center;
}

.picture {
  width: 180px; /* Slightly larger pictures */
  height: 180px;
  object-fit: cover;
  border-radius: 50%; /* Make them circular! */
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease, box-shadow 0.3s ease, border-color 0.3s ease;
  border: 4px solid #ffc0cb; /* Pink border for pictures */
}

.picture:hover {
  transform: scale(1.1) rotate(5deg); /* Pop and rotate! */
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.25);
  border-color: #ff69b4; /* Hot pink on hover */
}

/* Videos Section */
.videos-section {
  background: linear-gradient(135deg, #e6f7ff, #dcf0fb);
  border-radius: 15px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
  padding: 30px;
  margin-top: 25px;
  border: 1px solid #87ceeb; /* Sky blue border */
}

.videos-section h2 {
  font-size: 26px;
  margin-bottom: 20px;
  color: #1e90ff; /* Dodger blue for videos */
  text-align: center;
}

.videos-container {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.video {
  width: 320px; /* Slightly larger videos */
  height: auto;
  border-radius: 15px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.15);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  border: 3px solid #add8e6; /* Light blue border */
}

.video:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.25);
}

/* Navigation Bar */
.navbar {
  background-color: #ffb6c1; /* Light pink navbar */
  color: white;
  padding: 20px 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px; /* More rounded */
  margin-bottom: 30px;
  box-shadow: 0 5px 15px rgba(255, 182, 193, 0.4);
  position: sticky; /* Make it stick! */
  top: 0;
  z-index: 100; /* Ensure it's on top */
  animation: slideInDown 0.8s ease-out; /* Slide in from top */
}

@keyframes slideInDown {
  from {
      transform: translateY(-100%);
      opacity: 0;
  }
  to {
      transform: translateY(0);
      opacity: 1;
  }
}

.navbar h1 {
  margin: 0;
  font-size: 32px; /* Larger title */
  color: #ff69b4; /* Hot pink title */
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 25px; /* More space between links */
  margin: 0;
  padding: 0;
}

.nav-links li {
  margin: 0;
}

.nav-links a {
  color: #fff; /* White links */
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  transition: color 0.3s ease, transform 0.2s ease;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.1);
}

.nav-links a:hover {
  color: #ff69b4; /* Hot pink on hover */
  transform: translateY(-3px); /* Lift effect */
}